import { notify, notifyError } from "entities/notification/model";

import { getStore } from "./utils";
import { TSettings } from "entities/settings/types";
import { APP_STORE_NAME } from "./constants";

export function updateSettingsInDb(settings: TSettings) {
  let store = getStore(APP_STORE_NAME, true);
  let request = store.put(settings, "settings");
  request.onsuccess = function () {
    notify("Настройки успешно сохранены");
  };
  request.onerror = function () {
    notifyError("Не удалось сохранить настройки", request.error);
  };
}
